body {
  font-family: 'Segoe UI', Arial, sans-serif;
  background: #f8f9fa;
  margin: 0;
}

.page-container {
  padding: 32px 40px;
  background: #fff;
  min-height: 100vh;
}

.search-form {
  display: flex;
  gap: 20px;
  align-items: flex-end;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.search-form label {
  display: flex;
  flex-direction: column;
  font-weight: 500;
  font-size: 15px;
}

.search-form input,
.search-form select {
  margin-top: 4px;
  padding: 6px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.search-form button {
  padding: 8px 18px;
  background: #1976d2;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.search-form button:disabled {
  background: #90caf9;
  cursor: not-allowed;
}

.message {
  margin-bottom: 16px;
  color: #388e3c;
  font-weight: 500;
}

.tickets-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 18px;
  background: #fafafa;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}

.tickets-table th,
.tickets-table td {
  padding: 10px 12px;
  border: 1px solid #e0e0e0;
  text-align: left;
}

.tickets-table th {
  background: #1976d2;
  color: #fff;
  font-weight: 600;
}

.tickets-table tr:nth-child(even) {
  background: #f1f1f1;
}

.reupload-btn {
  margin-top: 8px;
  margin-bottom: 8px;
  padding: 8px 18px;
  background: #388e3c;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.reupload-btn:disabled {
  background: #a5d6a7;
  cursor: not-allowed;
}

.loading {
  margin-top: 18px;
  color: #1976d2;
  font-weight: 500;
}

.sidebar {
  width: 200px;
  height: 100vh;
  background: #1976d2;
  padding: 32px 20px;
  box-sizing: border-box;
  position: fixed;
  left: 0;
  top: 0;
  color: #fff;
  box-shadow: 2px 0 8px rgba(0,0,0,0.04);
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.sidebar-title {
  margin: 0 0 16px 0;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 1px;
}

.sidebar-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar-link {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  padding: 8px 0;
  display: block;
  border-radius: 4px;
  transition: background 0.2s;
}

.sidebar-link:hover {
  background: #1565c0;
}
